import React, { useEffect, useState } from 'react';

const date = new Date();
const greeting = () => {
    const hourpassed = date.getHours();
    if (hourpassed >= 0 && hourpassed <= 12) {
        return 'Good Morning';
    } else if (hourpassed > 12 && hourpassed <= 18) {
        return 'Good Afternoon';
    }

    return 'Good Evening';
};

const tempGreet = greeting();





function UpperBody() {
    const [greetingNote,SetGreetingNote]=useState("");
    setTimeout(()=>{
       
        
        SetGreetingNote(prevValue=>{
            return tempGreet.slice(0,prevValue.length+1);
        });
        
    },1000);
    
    
    return (
        <div className="content1">
            <h1 className="greetings">{greetingNote}
            </h1>
            <h2>Hey ,Its Abhishek</h2>
        </div>
    );
}
export default UpperBody;
